body {
  background-color: dimgray;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
.App-header,
.File-list div {
  color: white;
  text-shadow: 2px 2px 4px #000000; /* 검은색 그림자 추가 */
}

.App {
  text-align: center;
}

.App-header {
  background-color: transparent;
  padding: 20px;
  text-align: center;
}
  
.App-main {
  padding: 20px;
}

.Login {
  background-color:rgba(161, 147, 147, 0.622);
  padding: 20px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
  align-self: center;
}

.Login input {
  margin: 10px;
  padding: 10px;
  border: none;
  border-radius: 5px;
  width: 200px;
}

.File-operations {
  margin: auto;
  padding: 20px;
  max-width: 300px;
  
}

.File-operations .buttons {
  display: flex;
  justify-content: center;
  align-items: center;
}

.File-list div {
  cursor: pointer;
  margin-bottom: 10px;
}

.File-list div:hover {
  text-decoration: underline;
}

/* ... 기존 스타일링 */

.File-item {
  cursor: pointer;
  margin-bottom: 10px;
}

.File-item.selected {
  color: blue;
  font-weight: bold;
}

.File-item:hover {
  text-decoration: underline;
}

/* 공통 버튼 스타일 */
.button-style {
  background-color: transparent; /* 녹색 배경 */
  color: white;
  padding: 15px 20px;
  margin: 10px 0;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.button-style:hover {
  background-color: transparent;
}
/* 파일 업로드 라벨 스타일 */
.File-operations label {
  /* 라벨에 공통 버튼 스타일 적용 */
  display: inline-block;
  cursor: pointer;
  /* 추가 스타일링 */
}
/* 파일 업로드 버튼 스타일 */
.File-operations input[type="file"] {
  background-color: transparent;
  color: white;
  padding: 15px 20px;
  margin: 10px 0;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
/* 파일 업로드 라벨 스타일 */
.File-operations label {
  /* 라벨에 공통 버튼 스타일 적용 */
  display: inline-block;
  cursor: pointer;
  /* 추가 스타일링 */
}
.nav-links {
  position: fixed;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  color: white;
  text-shadow: 2px 2px 4px #000000;
  z-index: 1000; /* 추가: 다른 요소 위에 표시 */
}

.textarea-code {
  width: 100%; /* 혹은 특정 픽셀 값 */
  height: 200px; /* 원하는 높이로 설정 */
}


#code-formatter textarea {
  width: 100%; /* 전체 너비를 차지하도록 설정 */
  height: 400px; /* 높이를 300픽셀로 설정 */
}

@media (max-width: 768px) { /* 모바일 뷰포트를 위한 미디어 쿼리 */
  .nav-links {
    position: static; /* 모바일 화면에서는 고정 위치 사용 안 함 */
    transform: none;
    text-align: center;
  }

  .File-operations,
  .Login,
  .App-main,
  .File-list div,
  .File-item,
  .button-style,
  .File-operations input[type="file"],
  #code-formatter textarea {
    max-width: 100%; /* 모바일 화면에 맞게 최대 너비 설정 */
    box-sizing: border-box; /* 패딩과 테두리가 너비에 포함되도록 설정 */
  }

  #code-formatter textarea {
    height: 200px; /* 모바일 화면에 맞는 높이로 조정 */
  }
}